.container{
  width: 610px;
  min-height: auto;
  /* height: 100%; */
  margin: 0 auto;
  margin-top: 20px;
  padding-top: 20px;
  background-color: #0000007d;
  overflow: visible;
  border-radius: 15px;

}

@media (max-width:610px) {
  .container{
    width:500px;

  }
}
@media (max-width:550px) {
  .container{
    width:100%;
    min-height: 100%;
border-radius: 0;
     margin: 0;
  }
}
